.set-width{
    width: 25vw;
}
@font-face {
    font-family: Grotesk;
    src: url(/static/media/Grotesk.c13ab066.otf) format("opentype");
}

.logo {
    font-family: Grotesk;
    margin-bottom: 2rem;
}

a {
    text-decoration: none;
    color: var(--dark);
}

.filter-btn {
    border: none;
    background: none;
}

.filter-btn:focus {
    border: none;
    outline: none;
    color: var(--primary);
}

.filter-btn:focus{
    outline:none !important;
}

.spacing {
    padding: 1px 6px;
}

ul {
    list-style: none;
}

li {
    margin-bottom: 8px;
}

.under::after {
    content: "";
    display: block;
    width: 80px;
    height: 2px;
    background: var(--dark);
    bottom: 10px;
    left: 0;
}
@font-face {
    font-family: Grotesk, sans-serif;
    src: url(/static/media/Grotesk.c13ab066.otf);
}

:root {
    --primary: #723636;
    --background: #F4E7DA;
    --dark: #232323;
    --light: #fafafa;
    --error: rgb(228, 46, 1);
    /* --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem); */
}

* {
    box-sizing: border-box;
}

/* ========================================================================================== */

html,
body {
    min-height: 100%;

}

body {
    margin: 0;
    padding: 0;
    font-weight: 400;
    line-height: 1.5;
    font-size: 16px;
    color: #232323;
    color: var(--dark);
}

.page-bg {
    background-color: #F4E7DA;
    background-color: var(--background);
    width: 100vw;
    height: 100vh;
    font-family: 'Courier Prime', monospace;
}

a {
    text-decoration: none;
    color: #232323;
    color: var(--dark);
}

/* ========================================================================================== */

.com-con {
    border-style: solid;
    border-color: #232323;
    border-color: var(--dark);
    padding: 10px; 
}
